* {
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
      user-select: none;
  cursor: default;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  outline-width: 0;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

html, body {
  height: 100%;
  background-color: #080810;
  color: white;
}

.looper {
  margin: auto;
}

.header {
  padding: 10px 10px 0px 10px;
  margin-bottom: 75px;
  display: inline-block;
  width: 100%;
  background-color: #223;
}

.tempo-info {
  display: inline;
  float: left;
}

.tempo-info span {
  display: block;
  width: auto;
}

.title {
  font-size: 24pt;
  float: right;
}

.playback-toggle {
  position: relative;
}

.playback-toggle button {
  text-align: center;
  height: 50px;
  width: 60px;
  font-size: 24pt;
  cursor: pointer;
  border: none;
  -webkit-transform: translateY(-3px);
      -ms-transform: translateY(-3px);
          transform: translateY(-3px);
  color: white;
  background-color: rgba(0, 0, 0, 0);
}

.tempo-slider {
  width: 150px;
  border: solid 2px blue;
  cursor: pointer;
}

.grid-container {
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  margin: auto;
  overflow: auto;
  background-color: #223;
  border-radius: 3px;
  -webkit-box-shadow: 0 25px #181828;
          box-shadow: 0 25px #181828;
}

.grid {
  float: left;
}

.grid table {
  margin: auto;
  padding: 10px 7px;
}

.grid-button, .clear-button, .view-button {
  height: 30px;
  width: 25px;
  border: none;
  cursor: pointer;
  margin: 2px;
  border-radius: 3px;
  -webkit-transform: translateY(-3px);
      -ms-transform: translateY(-3px);
          transform: translateY(-3px);
}

.melody-grid .grid-button.on, .melody-view-button {
  -webkit-box-shadow: 0 3px #268;
          box-shadow: 0 3px #268;
  background-color: #8ac;
}

.bass-grid .grid-button.on, .bass-view-button {
  -webkit-box-shadow: 0 3px #484;
          box-shadow: 0 3px #484;
  background-color: #9c9;
}

.drum-grid .grid-button.on, .drums-view-button {
  -webkit-box-shadow: 0 3px #858;
          box-shadow: 0 3px #858;
  background-color: #c9c;
}

.grid-button.off {
  -webkit-box-shadow: 0 3px #444;
          box-shadow: 0 3px #444;
  background-color: #888;
}

.grid-button:active, .clear-button:active, .view-button:active {
  -webkit-transform: translateY(0px);
      -ms-transform: translateY(0px);
          transform: translateY(0px);
  -webkit-box-shadow: none;
          box-shadow: none;
}

.grid-button.off.beat {
  background-color: #aaa;
}

.melody-grid .grid-button.on.active {
  background-color: #bdf;
  -webkit-box-shadow: 0 3px #59b;
          box-shadow: 0 3px #59b;
}

.bass-grid .grid-button.on.active {
  background-color: #cfc;
  -webkit-box-shadow: 0 3px #7b7;
          box-shadow: 0 3px #7b7;
}

.drum-grid .grid-button.on.active {
  background-color: #fcf;
  -webkit-box-shadow: 0 3px #b7b;
          box-shadow: 0 3px #b7b;
}

.view-switcher {
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  margin: auto;
  text-align: center;
  padding: 15px 7px 0px 7px;
}

.view-button {
  margin: 0 4px;
}

.controls {
  float: right;
}

.clear-button {
  font-size: 14pt;
  padding: 0;
  margin: 15px 10px;
  color: #844;
  text-align: center;
  -webkit-box-shadow: 0 3px #844;
          box-shadow: 0 3px #844;
  background-color: #c99;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



